<template>
    <div>
        <div class="home-tab"
            v-for="tab in tabs">
            <div class="tab-container">
                <slick
                        ref="slick"
                        :options="slickOptions"
                        @afterChange="handleAfterChange"
                        @beforeChange="handleBeforeChange"
                        @breakpoint="handleBreakpoint"
                        @destroy="handleDestroy"
                        @edge="handleEdge"
                        @init="handleInit"
                        @reInit="handleReInit"
                        @setPosition="handleSetPosition"
                        @swipe="handleSwipe"
                        @lazyLoaded="handleLazyLoaded"
                        @lazyLoadError="handleLazeLoadError">
                    <div class="product-inner"
                        v-for="item in tab.items">
                        <div class="product-img">
                            <!--<img v-bind:src="item.img" alt="">-->
                            <img v-bind:src="item.img" alt="">
                        </div>
                        <div class="product-title">{{item.title}}</div>
                        <div class="product-desc">{{item.description}}</div>
                    </div>
                </slick>
            </div>
        </div>
    </div>
</template>

<script>
    import Slick from 'vue-slick';
    import SlickCss from '@/assets/scss/now-ui-kit/slick/slick.scss';
    import SlickTheme from '@/assets/scss/now-ui-kit/slick/slick-theme.scss';

export default {
    components: {Slick},
    data() {
        return {
            slickOptions: {
                slidesToShow: 4,
                // arrows : true,
                prevArrow : '<button type="button" class="slick-prev"><i class="el-icon-arrow-left"></i></button>',
                nextArrow : '<button type="button" class="slick-next"><i class="el-icon-arrow-right"></i></button>',
            },
            tabs : [
                {
                    id : 1,
                    name : 'Peplink',
                    items : [
                        {
                            title : 'Balance_20',
                            description: 'Balance_20',
                            url : '',
                            img : require('../../assets/public/img/products/Peplink/Peplink_Balance_20.png')
                        },
                        {
                            title : 'Peplink_Balance_30_LTE',
                            description: 'Peplink_Balance_30_LTE',
                            url : '',
                            img : require('../../assets/public/img/products/Peplink/Peplink_Balance_30_LTE.png')
                        },
                        {
                            title : 'Peplink_Balance_210',
                            description: 'Peplink_Balance_210',
                            url : '',
                            img : require('../../assets/public/img/products/Peplink/Peplink_Balance_210.png')
                        },
                        {
                            title : 'Peplink_Balance_305',
                            description: 'Peplink_Balance_305',
                            url : '',
                            img : require('../../assets/public/img/products/Peplink/Peplink_Balance_305.png')
                        },
                        {
                            title : 'Peplink_Balance_580',
                            description: 'Peplink_Balance_580',
                            url : '',
                            img : require('../../assets/public/img/products/Peplink/Peplink_Balance_580.png')
                        }
                    ]
                }
            ]
        };
    },
    css : {
        modules : true
    },

    // All slick methods can be used too, example here
    methods: {
        clickTab(tab){
          this.items = tab.items
            // this.$forceUpdate(this.items)
        },
        next() {
            this.$refs.slick.next();
        },

        prev() {
            this.$refs.slick.prev();
        },

        reInit() {
            // Helpful if you have to deal with v-for to update dynamic lists
            this.$nextTick(() => {
                this.$refs.slick.reSlick();
            });
        },

        // Events listeners
        handleAfterChange(event, slick, currentSlide) {
            console.log('handleAfterChange', event, slick, currentSlide);
        },
        handleBeforeChange(event, slick, currentSlide, nextSlide) {
            console.log('handleBeforeChange', event, slick, currentSlide, nextSlide);
        },
        handleBreakpoint(event, slick, breakpoint) {
            console.log('handleBreakpoint', event, slick, breakpoint);
        },
        handleDestroy(event, slick) {
            console.log('handleDestroy', event, slick);
        },
        handleEdge(event, slick, direction) {
            console.log('handleEdge', event, slick, direction);
        },
        handleInit(event, slick) {
            console.log('handleInit', event, slick);
        },
        handleReInit(event, slick) {
            console.log('handleReInit', event, slick);
        },
        handleSetPosition(event, slick) {
            console.log('handleSetPosition', event, slick);
        },
        handleSwipe(event, slick, direction) {
            console.log('handleSwipe', event, slick, direction);
        },
        handleLazyLoaded(event, slick, image, imageSource) {
            console.log('handleLazyLoaded', event, slick, image, imageSource);
        },
        handleLazeLoadError(event, slick, image, imageSource) {
            console.log('handleLazeLoadError', event, slick, image, imageSource);
        },
    },
}
</script>
<style>
    /*.product-inner {*/
        /*display:flex;*/
        /*flex-direction: colum;*/
    /*}*/
    .product-title {
        position: relative;
        text-align: left;
        text-transform: uppercase;
        font-size: 15px;
        font-weight: 600;
    }
    .product-desc {
        text-align: left;
    }
    .home-tab h2 {
        font-weight: 600;
        text-transform: uppercase;
    }

</style>


