import Vue from 'vue';
import Router from 'vue-router';
import Index from './pages/Index.vue';
import HomePage from './pages/Home.vue';
import Login from './pages/Login.vue';
import Profile from './pages/Profile.vue';
import MainNavbar from './layout/MainNavbar.vue';
import MainFooter from './layout/MainFooter.vue';
import LeftNavbar from './components/Navbar/NavLeftBar.vue'
import Menu from './pages/Menu.vue'

Vue.use(Router);

export default new Router({
  mode : 'history',
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'index',
      components: { default: HomePage, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 20 },
        footer: { backgroundColor: 'black' }
      }
    },
    {
        path: '/home',
        name: 'home',
        components: { default: HomePage, header: MainNavbar, footer: MainFooter },
        props: {
            header: { colorOnScroll: 20 },
            footer: { backgroundColor: 'black' }
        }
    },
      {
          path: '/menu',
          name: 'menu',
          components: { default: Menu, header: MainNavbar, footer: MainFooter },
          props: {
              header: { colorOnScroll: 20 },
              footer: { backgroundColor: 'black' }
          }
      },
    // {
    //   path: '/landing',
    //   name: 'landing',
    //   components: { default: Landing, header: MainNavbar, footer: MainFooter },
    //   props: {
    //     header: { colorOnScroll: 400 },
    //     footer: { backgroundColor: 'black' }
    //   }
    // },
    {
      path: '/login',
      name: 'login',
      components: { default: Login, header: MainNavbar },
      props: {
        header: { colorOnScroll: 400 }
      }
    },
    {
      path: '/profile',
      name: 'profile',
      components: { default: Profile, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 },
        footer: { backgroundColor: 'black' }
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});



