    flex-direction: column !important;
    padding: 5px;
    margin: 5px;
    position: relative;
      -webkit-box-shadow: 0 0 1px #bfbbbb;
      box-shadow: 0 0 1x #bfbbbb;
  }
  .product-img {
    position: relative;
    overflow: hidden;
    border-radius: 3px 3px 0 0;
    height: 200px;
  }
  .product-img img{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform .5s;
  }
  .product-title {
    position: relative;
    text-align: left;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 600;
  }
  .product-desc {
