  }
  .product-inner:hover  {
    border: solid 1px #ccc;
    /*-webkit-box-shadow: 0 1px -1px 1px #ccc;*/
    /*box-shadow: 0 1px -1px 1px #ccc;*/
    -webkit-box-shadow: 0 0 10px #bfbbbb;
    box-shadow: 0 0 10px #bfbbbb;
    /*margin: 6px;*/
  }
  .product-inner:hover img {
    transform: scale(1.2);
  }
  .m-10 {
    margin: 10px;
  }
  .slick-slide {
    width: 269px !important;
      margin-left: 3px;
      margin-right: 5px;
  }
  .carousel-caption {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
    bottom: 0px !important;
  }
  .carousel-caption h5 {
    font-weight: 400;
    text-transform: capitalize;
    font-size: 20px;
  }
  .capitalize {
    text-transform: capitalize;
  }
  .container-custom {
    max-width: 1750px;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
    .bg-about-us{
        background-color: #2c2c2c;
        color: #FFFFFF;
    }
    .section-about-us .description {
        color: #FFFFFF !important;
    }
</style>



